2 */\n}\n//stylelint-enable\n/**\n * Add the correct font size in all browsers.\n */\n\nsmall {\n  font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` elements from affecting the line height in\n * all browsers.\n */\n\nsub,\nsup {\n  position: relative;\n  font-size: 75%;\n  line-height: 0;\n  vertical-align: baseline;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\nsup {\n  top: -0.5em;\n}\n\nol, ul, li {\n  list-style: none;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove the border on images inside links in IE 10.\n */\n\nimg {\n  border-style: none;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * 1. Change the font styles in all browsers.\n * 2. Remove the margin in Firefox and Safari.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n  margin: 0; /* 2 */\n  font-size: 100%; /* 1 */\n  font-family: inherit; /* 1 */\n  line-height: 1.15; /* 1 */\n}\n\n/**\n * Show the overflow in IE.\n * 1. Show the overflow in Edge.\n */\n\nbutton,\ninput { /* 1 */\n  overflow: visible;\n}\n\n/**\n * Remove the inheritance of text transform in Edge, Firefox, and IE.\n * 1. Remove the inheritance of text transform in Firefox.\n */\n\nbutton,\nselect { /* 1 */\n  text-transform: none;\n}\n\n/**\n * Correct the inability to style clickable types in iOS and Safari.\n */\n\nbutton,\n[type=\"button\"],\n[type=\"reset\"],\n[type=\"submit\"] {\n  -webkit-appearance: button;\n}\n\n/**\n * Remove the inner border and padding in Firefox.\n */\n\nbutton::-moz-focus-inner,\n[type=\"button\"]::-moz-focus-inner,\n[type=\"reset\"]::-moz-focus-inner,\n[type=\"submit\"]::-moz-focus-inner {\n  padding: 0;\n  border-style: none;\n}\n\n/**\n * Restore the focus styles unset by the previous rule.\n */\n\nbutton:-moz-focusring,\n[type=\"button\"]:-moz-focusring,\n[type=\"reset\"]:-moz-focusring,\n[type=\"submit\"]:-moz-focusring {\n  outline: 1px dotted ButtonText;\n}\n\n/**\n * Correct the padding in Firefox.\n */\n\nfieldset {\n  padding: 0.35em 0.75em 0.625em;\n}\n\n/**\n * 1. Correct the text wrapping in Edge and IE.\n * 2. Correct the color inheritance from `fieldset` elements in IE.\n * 3. Remove the padding so developers are not caught out when they zero out\n *    `fieldset` elements in all browsers.\n */\n\nlegend {\n  display: table; /* 1 */\n  box-sizing: border-box; /* 1 */\n  max-width: 100%; /* 1 */\n  padding: 0; /* 3 */\n  color: inherit; /* 2 */\n  white-space: normal; /* 1 */\n}\n\n/**\n * Add the correct vertical alignment in Chrome, Firefox, and Opera.\n */\n\nprogress {\n  vertical-align: baseline;\n}\n\n/**\n * Remove the default vertical scrollbar in IE 10+.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * 1. Add the correct box sizing in IE 10.\n * 2. Remove the padding in IE 10.\n */\n\n[type=\"checkbox\"],\n[type=\"radio\"] {\n  box-sizing: border-box; /* 1 */\n  padding: 0; /* 2 */\n}\n\n/**\n * Correct the cursor style of increment and decrement buttons in Chrome.\n */\n\n[type=\"number\"]::-webkit-inner-spin-button,\n[type=\"number\"]::-webkit-outer-spin-button {\n  height: auto;\n}\n\n/**\n * 1. Correct the odd appearance in Chrome and Safari.\n * 2. Correct the outline style in Safari.\n */\n\n[type=\"search\"] {\n  outline-offset: -2px; /* 2 */\n  -webkit-appearance: textfield; /* 1 */\n}\n\n/**\n * Remove the inner padding in Chrome and Safari on macOS.\n */\n\n[type=\"search\"]::-webkit-search-decoration {\n  -webkit-appearance: none;\n}\n\n/**\n * 1. Correct the inability to style clickable types in iOS and Safari.\n * 2. Change font properties to `inherit` in Safari.\n */\n\n::-webkit-file-upload-button {\n  font: inherit; /* 2 */\n  -webkit-appearance: button; /* 1 */\n}\n\n/* Interactive\n   ========================================================================== */\n\n/*\n * Add the correct display in Edge, IE 10+, and Firefox.\n */\n\ndetails {\n  display: block;\n}\n\n/*\n * Add the correct display in all browsers.\n */\n\nsummary {\n  display: list-item;\n}\n\n/* Misc\n   ========================================================================== */\n\n/**\n * Add the correct display in IE 10+.\n */\n\ntemplate {\n  display: none;\n}\n\n/**\n * Add the correct display in IE 10.\n */\n\n[hidden] {\n  display: none !important;\n}\n","//\n//  DIALTONE\n//  TOKENS: SPACING\n//\n//  These are spacing tokens for Dialpad's design system Dialtone.\n//  For further documentation of these values, please visit:\n//  https://dialpad.design/tokens/spacing\n//\n//  ============================================================================\n//  $   SPACING UNITS\n//  ----------------------------------------------------------------------------\n@su0:                           0;          // 0\n@su1:                           0.1rem;     // 1px\n@su2:                           0.2rem;     // 2px\n@su4:                           0.4rem;     // 4px\n@su6:                           0.6rem;     // 6px\n@su8:                           0.8rem;     // 8px\n@su12:                          1.2rem;     // 12px\n@su16:                          1.6rem;     // 16px\n@su24:                          2.4rem;     // 24px\n@su32:                          3.2rem;     // 32px\n@su48:                          4.8rem;     // 48px\n@su64:                          6.4rem;     // 64px\n@su72:                          7.2rem;     // 72px\n@su84:                          8.4rem;     // 84px\n@su96:                          9.6rem;     // 96px\n@su102:                         10.2rem;    // 102px\n@su114:                         11.4rem;    // 114px\n@su128:                         12.8rem;    // 128px\n\n@spacing-units: {\n    su0:    @su0;\n    su1:    @su1;\n    su2:    @su2;\n    su4:    @su4;\n    su6:    @su6;\n    su8:    @su8;\n    su12:   @su12;\n    su16:   @su16;\n    su24:   @su24;\n    su32:   @su32;\n    su48:   @su48;\n    su64:   @su64;\n    su72:   @su72;\n    su84:   @su84;\n    su96:   @su96;\n    su102:  @su102;\n    su114:  @su114;\n    su128:  @su128;\n\n    sun1:   -(@su1);\n    sun2:   -(@su2);\n    sun4:   -(@su4);\n    sun6:   -(@su6);\n    sun8:   -(@su8);\n    sun12:  -(@su12);\n    sun16:  -(@su16);\n    sun24:  -(@su24);\n    sun32:  -(@su32);\n    sun48:  -(@su48);\n    sun64:  -(@su64);\n    sun72:  -(@su72);\n    sun84:  -(@su84);\n    sun96:  -(@su96);\n    sun102: -(@su102);\n    sun114: -(@su114);\n    sun128: -(@su128);\n}\n\nbody {\n    //  Spacing units\n    each(@spacing-units, {\n        --@{key}: @value;\n    });\n}\n\n#d-internals #spacing-classes(0.8rem, 'space');\n#d-internals #spacing-classes(1.6rem, 'space', '-16');\n#d-internals #spacing-classes(3.2rem, 'space', '-32');\n#d-internals #spacing-classes(6.4rem, 'space', '-64');\n#d-internals #spacing-classes(12.8rem, 'space', '-128');\n","//\n//  DIALTONE\n//  INTERNAL MIXINS\n//\n//  These are internal mixins to help generate various classes.\n//\n//  TABLE OF CONTENTS\n//   AUTO GENERATING CLASSES\n//\n//  ============================================================================\n//  $   CONFIGURATION SETTINGS\n//      Various configuration settings to generate the classes desired.\n//  ----------------------------------------------------------------------------\n#d-internal-config {\n    //  Do we want to generate font-face CSS?\n    @generate-font-face:             true;\n\n    //  Path of the fonts that Dialtone provides\n    @define-font-path: '../../fonts/';\n}\n\n//  ============================================================================\n//  $   INTERNAL MIXINS\n//      Various mixins used to build Dialtone\n//  ----------------------------------------------------------------------------\n#d-internals() {\n    //  ========================================================================\n    //  $   COLOR CLASS GENERATOR\n    //      Create a mixin to generate all the utility classes required. By doing it\n    //      this way, we are disabling the ability to use Dialtone utility classes as\n    //      a mixin. Previously we had allowed for utility classes to be used as\n    //      mixins, but because our overall CSS bundle size is growing exponentially,\n    //      Dialtone v6 removed this ability. It is now recommended to use CSS\n    //      variables (var(--color)). Less variables (@color) are also available,\n    //      but their use is discouraged.\n    //\n    //      USAGE:\n    //      #d-internals #color-classes(purple-500);\n    //\n    //  ------------------------------------------------------------------------\n    #color-classes(@color-name) {\n      //  --  FONT STYLES\n      .d-fc-@{color-name},\n      .h\\:d-fc-@{color-name}:hover,\n      .f\\:d-fc-@{color-name}:focus {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      .f\\:d-fc-@{color-name}:focus-within {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      .fv\\:d-fc-@{color-name}:focus-visible {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-fc-@{color-name}', {\n        --fco: 100%;\n        color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--fco)) !important;\n      });\n\n\n      //  --  BORDER STYLES\n      .d-bc-@{color-name},\n      .h\\:d-bc-@{color-name}:hover,\n      .f\\:d-bc-@{color-name}:focus {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      .f\\:d-bc-@{color-name}:focus-within {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      .fv\\:d-bc-@{color-name}:focus-visible {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bc-@{color-name}', {\n        --bco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bco)) !important;\n      });\n\n      //  --  DIVIDER COLORS\n      .d-divide-@{color-name} > * + * {\n        --dco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--dco)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-divide-@{color-name} > * + *', {\n        --dco: 100%;\n        border-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--dco)) !important;\n      });\n\n      //  --  BACKGROUND STYLES\n      .d-bgc-@{color-name},\n      .h\\:d-bgc-@{color-name}:hover,\n      .f\\:d-bgc-@{color-name}:focus {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      .f\\:d-bgc-@{color-name}:focus-within {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      .fv\\:d-bgc-@{color-name}:focus-visible {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgc-@{color-name}', {\n        --bgo: 100%;\n        background-color: hsla(var(~'--@{color-name}-h') var(~'--@{color-name}-s') var(~'--@{color-name}-l') ~' / ' var(--bgo)) !important;\n      });\n    }\n\n    //  ========================================================================\n    //  $   STANDARD WEIGHT LOOP GENERATOR\n    //      Since most of our colors have similar stops (100 - 600), we'll save\n    //      ourselves the need to call the mixin for each color and create a\n    //      loop that will create most of the colors for us.\n    //\n    //      USAGE:\n    //      #d-internals #standard-stops(purple);\n    //\n    //  ------------------------------------------------------------------------\n    #standard-stops(@color) {\n      #d-internals #color-classes(~'@{color}-600');\n      #d-internals #color-classes(~'@{color}-500');\n      #d-internals #color-classes(~'@{color}-400');\n      #d-internals #color-classes(~'@{color}-300');\n      #d-internals #color-classes(~'@{color}-200');\n      #d-internals #color-classes(~'@{color}-100');\n    }\n\n    //  ========================================================================\n    //  $   HOVER / FOCUS GENERATOR\n    //      Pass a class name and mixin with the properties to copy into this function\n    //      to generate hover / focus copies of it prefixed with h: and f:.\n    //      Many of the class generators in this file will already do this,\n    //      so this mixin is meant more for one off exceptions.\n    //\n    //      USAGE:\n    //      #d-internals #generate-hover-focus(d-bgc-transparent, {.d-bgc-transparent();});\n    //\n    //  ------------------------------------------------------------------------\n    #generate-hover-focus(@className, @mixin) {\n      .h\\:@{className}:hover {\n        @mixin();\n      }\n      .f\\:@{className}:focus {\n        @mixin();\n      }\n      .f\\:@{className}:focus-within {\n        @mixin();\n      }\n      .fv\\:@{className}:focus-visible {\n        @mixin();\n      }\n    }\n\n    //  ========================================================================\n    //  $   OPACITY CLASS GENERATOR\n    //      Creates opacity classes for font, background, and border colors.\n    //\n    //      USAGE:\n    //      #d-internals #opacity-classes(50);\n    //\n    //  ------------------------------------------------------------------------\n    #opacity-classes(@opacity) {\n      //  --  FONT OPACITY\n      .d-fco@{opacity},\n      .h\\:d-fco@{opacity}:hover,\n      .f\\:d-fco@{opacity}:focus {\n        --fco: ~'@{opacity}%' !important;\n      }\n      .f\\:d-fco@{opacity}:focus-within {\n        --fco: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-fco@{opacity}:focus-visible {\n        --fco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-fco@{opacity}', {\n        --fco: ~'@{opacity}%' !important;\n      });\n\n\n      //  --  BORDER OPACITY\n      .d-bco@{opacity},\n      .h\\:d-bco@{opacity}:hover,\n      .f\\:d-bco@{opacity}:focus {\n        --bco: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bco@{opacity}:focus-within {\n        --bco: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bco@{opacity}:focus-visible {\n        --bco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bco@{opacity}', {\n        --bco: ~'@{opacity}%' !important;\n      });\n\n      //  --  DIVIDER OPACITY\n      .d-dco@{opacity} > * + * {\n        --dco: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-dco@{opacity} > * + *', {\n        --dco: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND COLOR OPACITY\n      .d-bgo@{opacity},\n      .h\\:d-bgo@{opacity}:hover,\n      .f\\:d-bgo@{opacity}:focus {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgo@{opacity}:focus-within {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgo@{opacity}:focus-visible {\n        --bgo: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgo@{opacity}', {\n        --bgo: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND GRADIENT STARTING STOP\n      .d-bgg-from-o@{opacity},\n      .h\\:d-bgg-from-@{opacity}:hover,\n      .f\\:d-bgg-from-@{opacity}:focus {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgg-from-@{opacity}:focus-within {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgg-from-@{opacity}:focus-visible {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{opacity}', {\n        --bgg-from-opacity: ~'@{opacity}%' !important;\n      });\n\n      //  --  BACKGROUND GRADIENT ENDING STOP\n      .d-bgg-to-o@{opacity},\n      .h\\:d-bgg-to-@{opacity}:hover,\n      .f\\:d-bgg-to-@{opacity}:focus {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      .f\\:d-bgg-to-@{opacity}:focus-within {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      .fv\\:d-bgg-to-@{opacity}:focus-visible {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-to-@{opacity}', {\n        --bgg-to-opacity: ~'@{opacity}%' !important;\n      });\n    }\n    //  ========================================================================\n    //  $   COLOR VARS\n    //      #color-vars creates all our HSL CSS color vars for us from a set\n    //      of HEX codes.\n    //\n    //      USAGE:\n    //      #d-internals #color-vars(@black-900);\n    //\n    //  ------------------------------------------------------------------------\n    #color-vars(@name, @color) {\n        --@{name}-h: hue(@color);\n        --@{name}-s: saturation(@color);\n        --@{name}-l: lightness(@color);\n        --@{name}-hsl: ~\"var(--@{name}-h) var(--@{name}-s) var(--@{name}-l)\";\n        --@{name}: hsla(~\"var(--@{name}-h) var(--@{name}-s) var(--@{name}-l) /  var(--alpha, 100%)\");\n    }\n    #dark-color-vars(@name, @color) {\n\n    }\n\n    //  ========================================================================\n    //  $   DARK MODE\n    //      #dark-mode creates a dark-mode override behind the\n    //      `prefers-color-scheme: dark` media query and `.theme-system` class.\n    //      It also renders the same rules outside of the media query with\n    //      `.theme-dark`.\n    //\n    //      USAGE:\n    //      #d-internals #dark-mode('.d-modal', { background-color: var(--black-400); })\n    //\n    //  ------------------------------------------------------------------------\n    #dark-mode(@class, @rules) {\n        @nodot-s: replace(@class, \"^\\.\", \"\");\n        @nodot: ~\"@{nodot-s}\";\n        @classname: ~\"@{class}\";\n\n        body.theme-system @{classname} {\n            @media (prefers-color-scheme: dark) {\n                @rules();\n            }\n        }\n        body.theme-dark @{classname} {\n            @rules();\n        }\n\n        #d-internals-collect-dark() {\n            .d\\:@{nodot} {\n                @rules();\n            }\n        }\n    }\n\n\n    //  ========================================================================\n    //  $   AUTO GENERATE CLASSES\n    //      This mixin tool is used to help generate a bunch of related classes,\n    //      usually utility classes.\n    //  ------------------------------------------------------------------------\n    #auto-generate(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @px: (@val * 10); // Convert rem values to px equilavent\n            @unitless: unit(@px);\n\n            //  ...and less than 0\n            & when (@unitless < 0) {\n                @abs: abs(@unitless);\n                @class-name: ~\"@{class-prefix}n@{abs}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n\n            //  ...and equal to or greater than 0\n            & when (@unitless >= 0) {\n                @class-name: ~\"@{class-prefix}@{unitless}\";\n\n                @{class-name} { @rules(); .template(@val); }\n            }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index(@class-prefix, @rules, @index + 1, @values);\n    }\n\n    #auto-generate-no-convert(@class-prefix, @rules, @values) {\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, 1, @values);\n    }\n    #auto-generate-with-index-no-convert(@class-prefix, @rules, @index, @values) when (@index <= length(@values)) {\n        // Extract our values\n        @val: extract(@values, @index);\n\n        //  When our values are numeric...\n        & when (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        //  When our values aren't numeric...\n        & when not (isnumber(@val)) {\n            @class-name: ~\"@{class-prefix}-@{val}\";\n\n            @{class-name} { @rules(); .template(@val); }\n        }\n\n        #d-internals #auto-generate-with-index-no-convert(@class-prefix, @rules, @index + 1, @values);\n    }\n\n\n    //  ========================================================================\n    //  $   CREATE FONT FACE\n    //  ------------------------------------------------------------------------\n    #font-face(@type, @name, @style: normal, @local: false, @ascent: normal) {\n        #d-internal-config();\n\n        if((@generate-font-face = true), each(@type, {\n          @font-face {\n            font-style: @style;\n            font-weight: @key;\n            font-family: @name;\n            src: if((@local = true ), local(@value), url(\"@{define-font-path}@{value}.woff2\") format(\"woff2\"));\n            ascent-override: @ascent;\n          };\n        }));\n    }\n\n    #gradient-classes(@color) {\n      //  COLOR STOP 1, STARTING STOP\n      .d-bgg-from-@{color},\n      .h\\:d-bgg-from-@{color}:hover,\n      .f\\:d-bgg-from-@{color}:focus {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .f\\:d-bgg-from-@{color}:focus-within {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      .fv\\:d-bgg-from-@{color}:focus-visible {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-from-@{color}', {\n        --bgg-from-opacity: 100%;\n        --bgg-from: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-from-opacity));\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" 0%);\n      });\n\n      //  COLOR STOP 2, ENDING STOP\n      .d-bgg-to-@{color},\n      .h\\:d-bgg-to-@{color}:hover,\n      .f\\:d-bgg-to-@{color}:focus {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .f\\:d-bgg-to-@{color}:focus-within {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      .fv\\:d-bgg-to-@{color}:focus-visible {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      }\n      #d-internals #dark-mode('.d\\:d-bgg-to-@{color}', {\n        --bgg-to-opacity: 100%;\n        --bgg-to: hsla(var(~\"--@{color}-h\") var(~\"--@{color}-s\") var(~\"--@{color}-l\") ~\" / \" var(--bgg-to-opacity)) !important;\n      });\n    }\n\n    #gradient-stops(@color) {\n      #d-internals #gradient-classes(~\"@{color}-600\");\n      #d-internals #gradient-classes(~\"@{color}-500\");\n      #d-internals #gradient-classes(~\"@{color}-400\");\n      #d-internals #gradient-classes(~\"@{color}-300\");\n      #d-internals #gradient-classes(~\"@{color}-200\");\n      #d-internals #gradient-classes(~\"@{color}-100\");\n    }\n\n    #build-flex-columns(@num, @count: 1) when (@count =< @num) {\n    .d-fl-col@{count} {\n      display: flex;\n\n      //  Automatically make all direct child to obey the parent class\n      > * {\n        --fl-gap: 0;\n        --fl-basis: calc(100% / @count);\n\n        display: inline-flex;\n        margin: var(--fl-gap);\n        flex: 1 calc(var(--fl-basis) - ((var(--fl-gap) * 2)));\n\n        //  Add in a gap to items after the first row\n        &:nth-child(-n + @{count}) {\n          margin-top: 0;\n        }\n      }\n\n      //  If flex column is in a column direction (and became rows),\n      //  change the margin direction.\n      &.d-fd-column > * {\n        margin: var(--fl-gap) 0;\n      }\n    }\n\n    //  Iterate our loop until we hit our grid column total\n    #d-internals #build-flex-columns(@num, @count + 1);\n  }\n\n    //  ========================================================================\n    //  $   SPACING CLASSES\n    //  ------------------------------------------------------------------------\n    #spacing-classes(@BU, @prefix, @suffix: '') {\n      @stop-100: ~\"@{prefix}-100@{suffix}\";\n      @stop-200: ~\"@{prefix}-200@{suffix}\";\n      @stop-300: ~\"@{prefix}-300@{suffix}\";\n      @stop-400: ~\"@{prefix}-400@{suffix}\";\n      @stop-500: ~\"@{prefix}-500@{suffix}\";\n      @stop-600: ~\"@{prefix}-600@{suffix}\";\n      @stop-700: ~\"@{prefix}-700@{suffix}\";\n      @stop-800: ~\"@{prefix}-800@{suffix}\";\n\n      body {\n        --@{stop-100}:       calc(@BU / pow(2, 3));\n        --@{stop-200}:       calc(@BU / pow(2, 2));\n        --@{stop-300}:       calc(@BU / pow(2, 1));\n        --@{stop-400}:       @BU;\n        --@{stop-500}:       calc(@BU * pow(2, 1));\n        --@{stop-600}:       calc(@BU * pow(2, 2));\n        --@{stop-700}:       calc(@BU * pow(2, 3));\n        --@{stop-800}:       calc(@BU * pow(2, 4));\n      }\n    }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: Avatars\n//\n//  These are avatars classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/avatars\n//\n//  TABLE OF CONTENTS\n//   BASE STYLE\n//     SIZES\n//     COLORS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n.d-avatar {\n  //  Component CSS Vars\n  //  ------------------------------------------------------------------------\n  --avatar-color-background: var(--gold-100);\n  --avatar-color-text: var(--fc-secondary);\n  --avatar-gradient-angle: 180deg;\n  --avatar-gradient-stop-1: var(--gold-100);\n  --avatar-gradient-stop-2: var(--magenta-100);\n  --avatar-gradient-stop-3: var(--blue-200);\n  --avatar-gradient:\n    conic-gradient(\n      from var(--avatar-gradient-angle) at 50% 50%,\n      var(--avatar-gradient-stop-1) 0deg,\n      var(--avatar-gradient-stop-2) 180deg,\n      var(--avatar-gradient-stop-3) 360deg\n    );\n  --avatar-size-shape: var(--size-600);\n  --avatar-size-text: var(--fs-200);\n  --avatar-presence-position-right: calc(var(--space-200) * -1); // -2\n  --avatar-presence-position-bottom: calc(var(--space-200) * -1); // -2\n\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  width: var(--avatar-size-shape);\n  height: var(--avatar-size-shape);\n  color: var(--avatar-color-text);\n  font-weight: var(--fw-bold);\n  font-size: var(--avatar-size-text);\n  line-height: var(--lh-100);\n  text-transform: uppercase;\n  background-color: var(--avatar-color-background);\n  background-image: var(--avatar-gradient);\n  border-radius: var(--br-pill);\n\n  &__image {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    border-radius: inherit;\n  }\n\n  &__presence {\n    position: absolute;\n    right: var(--avatar-presence-position-right);\n    bottom: var(--avatar-presence-position-bottom);\n    display: flex;\n  }\n\n  //  --  SIZES\n  //  ------------------------------------------------------------------------\n\n  &--xs {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-200)); // 18\n    --avatar-presence-position-right: calc(var(--space-300) * -1); // -4\n    --avatar-presence-position-bottom: calc(var(--space-300) * -1); // -4\n  }\n\n  &--sm {\n    --avatar-size-shape: calc(var(--size-500) + var(--size-400)); // 24\n    --avatar-size-text: var(--fs-100);\n    --avatar-presence-position-right: calc(var(--space-200) * -1); // -2\n    --avatar-presence-position-bottom: calc(var(--space-200) * -1); // -2\n  }\n\n  &--md {\n    --avatar-size-shape: var(--size-600); // 32\n    --avatar-size-text: var(--fs-200);\n    --avatar-presence-position-right: calc(var(--space-100) * -1); // -1\n    --avatar-presence-position-bottom: calc(var(--space-100) * -1); // -1\n  }\n\n  &--lg {\n    --avatar-size-shape: calc(var(--size-600) + var(--size-500)); // 48\n    --avatar-size-text: var(--fs-300);\n    --avatar-presence-position-right: var(--space-100); // 1\n    --avatar-presence-position-bottom: var(--space-100); // 1\n  }\n\n  &--xl {\n    --avatar-size-shape: var(--size-700); // 64\n    --avatar-size-text: var(--fs-400);\n    --avatar-presence-position-right: var(--space-300); // 4\n    --avatar-presence-position-bottom: var(--space-300); // 4\n  }\n}\n","//\n//  DIALTONE\n//  COMPONENTS: BADGES\n//\n//  These are badge classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/components/badges\n//\n//  TABLE OF CONTENTS\n//   COMPONENT CSS VARIABLES\n//   BASE STYLE\n//   KIND\n//   TYPE\n//   SLOTS\n//\n//  ============================================================================\n//  $   BASE STYLE\n//  ----------------------------------------------------------------------------\n\n.d-badge {\n  //  COMPONENT CSS VARIABLES\n  //  --------------------------------------------------------------------------\n  --badge-color-text: var(--fc-primary);\n  --badge-color-background: var(--black-200);\n  --badge-color-background-ai: linear-gradient(to bottom right, var(--purple-400) 0%, var(--magenta-300) 100%);\n  --badge-radius: var(--size-300);\n  --badge-line-height: var(--size-500);\n  --badge-font-size: var(--fs-100);\n  --badge-font-weight: var(--fw-semibold);\n  --badge-gap: var(--space-300);\n  --badge-letter-spacing: calc(var(--size-100) / 2);\n  --badge-padding-y: var(--space-100);\n  --badge-padding-x: var(--space-400);\n  --badge-text-case: none;\n\n  //  BASE STYLE\n  //  --------------------------------------------------------------------------\n  display: inline-flex;\n  gap: var(--badge-gap);\n  align-items: center;\n  justify-content: center;\n  box-sizing: border-box;\n  min-width: calc(var(--size-400) + var(--size-500));\n  padding: var(--badge-padding-y) var(--badge-padding-x);\n  color: var(--badge-color-text);\n  font-weight: var(--badge-font-weight);\n  font-size: var(--badge-font-size);\n  line-height: var(--badge-line-height);\n  letter-spacing: var(--badge-letter-spacing);\n  text-align: center;\n  text-transform: var(--badge-text-case);\n  background-color: var(--badge-color-background);\n  border-radius: var(--badge-radius);\n\n  //  Kind\n  //  --------------------------------------------------------------------------\n\n  &--count {\n    --badge-radius: var(--br-pill);\n    --badge-padding-x: calc(var(--space-400) - var(--space-100));\n    --badge-padding-y: var(--space-300);\n  }\n\n  //  TYPE\n  //  --------------------------------------------------------------------------\n\n  &--info {\n    --badge-color-background: var(--blue-100);\n  }\n\n  &--success {\n    --badge-color-background: var(--green-100);\n  }\n\n  &--warning {\n    --badge-color-background: var(--gold-100);\n  }\n\n  &--critical {\n    --badge-color-background: var(--red-100);\n  }\n\n  &--bulletin {\n    --badge-color-text: var(--white);\n    --badge-color-background: var(--purple-400);\n  }\n\n  &--ai {\n    --badge-color-text: var(--white);\n    --badge-color-background: var(--magenta-300);\n\n    background-image: var(--badge-color-background-ai);\n  }\n\n  //  SLOTS\n  //  --------------------------------------------------------------------------\n\n  &__label {\n    display: flex;\n  }\n\n  &__icon-left,\n  &__icon-right {\n    display: flex;\n  }\n}\n","//@import (reference) '../utilities/spacing.less';\n\n//\n//  DIALTONE\n//  COMPONENTS: BANNERS\n//\n//  These are the styles for banners, which are a type of notice used\n//  to alert users about important information relevant to them.\n//  For further documentation of these styles, please visit:\n//  https://dialpad.design/components/banners\n//\n//  ============================================================================\n//  $   BASE WRAPPER\n//  ----------------------------------------------------------------------------\n.d-banner {\n    //  Component CSS Vars\n    //  ------------------------------------------------------------------------\n    --banner--bgc: var(--black-100);\n    --banner--fc: var(--fc-primary);\n\n    position: fixed;\n    top: 0;\n    right: 0;\n    left: 0;\n    z-index: var(--zi-navigation-fixed);\n    display: flex;\n    box-sizing: border-box;\n    width: 100%;\n    min-height: calc(var(--size-300) * 12); // 48\n    color: var(--banner--fc);\n    font-size: var(--fs-200);\n    line-height: var(--lh6);\n    background-color: var(--banner--bgc);\n    border-radius: 0;\n    box-shadow: none;\n\n    // If you want to hide and reveal the banner\n    &[aria-hidden='true'] {\n        --topbar-height: calc(var(--size-3) * 16); // -64\n\n        visibility: hidden;\n        opacity: 0;\n    }\n\n    &[aria-hidden='false'] {\n        visibility: visible;\n        opacity: 1;\n    }\n\n    //  If you want to put the banner above the topbar\n    &.d-banner--pinned {\n        z-index: calc(~'var(--zi-navigation-fixed) + 1');\n        transform: translate3d(0, 0, 0);\n    }\n}\n\n.d-banner--transform {\n    transform: translate3d(0, var(--topbar-height), 0);\n}\n\n//  ============================================================================\n//  $   CONTENT CONTAINER\n//  ----------------------------------------------------------------------------\n.d-banner__dialog {\n    position: relative;\n    display: flex;\n    align-items: center;\n    width: 100%;\n    max-width: 128rem;\n    min-height: 100%;\n    margin: 0 auto;\n    padding: var(--space-400);\n\n    .d-notice__content {\n        flex-direction: row;\n        align-items: center;\n\n        .d-flow4();\n    }\n}\n\n//  ============================================================================\n//  $   STYLES\n//  ============================================================================\n//  $$  DEFAULT IMPORTANT\n//  ----------------------------------------------------------------------------\n.d-banner.d-banner--important {\n    --banner--bgc: var(--black-800);\n    --banner--fc: var(--black-100);\n}\n\n//  $$  ERROR\n//  ----------------------------------------------------------------------------\n.d-banner--error {\n    --banner--bgc: var(--red-100);\n    --banner--fc: var(--red-500);\n\n    &.d-banner--important {\n        --banner--bgc: var(--red-300);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  INFO\n//  ----------------------------------------------------------------------------\n.d-banner--info {\n    --banner--bgc: var(--purple-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--purple-400);\n        --banner--fc: var(--white);\n    }\n}\n\n//  $$  SUCCESS\n//  ----------------------------------------------------------------------------\n.d-banner--success {\n    --banner--bgc: var(--green-100);\n    --banner--fc: var(--green-500);\n\n    &.d-banner--important {\n        --banner--bgc: var(--green-200);\n        --banner--fc: var(--green-500);\n    }\n}\n\n//  $$  WARNING\n//  ----------------------------------------------------------------------------\n.d-banner--warning {\n    --banner--bgc: var(--gold-100);\n\n    &.d-banner--important {\n        --banner--bgc: var(--gold-200);\n        --banner--fc: var(--fc-primary);\n    }\n}\n","//@import (reference) '../variables/spacing.less';\n//@import (reference) 'internals.less';\n\n//\n//  DIALTONE\n//  UTILITIES: SPACING\n//\n//  These are spacing utility classes for Dialpad's design system Dialtone.\n//  For further documentation of these and other classes,\n//  visit https://dialpad.design/utilities/spacing\n//\n//  TABLE OF CONTENTS\n//   AUTO-SPACING\n//    - VERTICALLY\n//    - HORIZONTALLY\n//   MARGIN\n//    - POSITIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//    - NEGATIVE MARGINS\n//       ALL DIRECTIONS\n//       TOP MARGIN\n//       RIGHT MARGIN\n//       BOTTOM MARGIN\n//       LEFT MARGIN\n//       X-AXIS MARGIN\n//       Y-AXIS MARGIN\n//   PADDING\n//    - ALL DIRECTIONS\n//    - TOP PADDING\n//    - RIGHT PADDING\n//    - BOTTOM PADDING\n//    - LEFT PADDING\n//    - X-AXIS PADDING\n//    - Y-AXIS PADDING\n//    - RESET PADDING\n//\n//  ============================================================================\n//  $   AUTO-SPACING\n//  ============================================================================\n//  $$  THE STACK\n//      Instead of stlying specific elements when we're unaware of their context,\n//      this applies margin to context. The margin is only applied when an\n//      element is proceeded by another element.\n//  ----------------------------------------------------------------------------\n.d-stack0 { > * + * { margin-top: @su0 !important; } }\n.d-stack1 { > * + * { margin-top: @su1 !important; } }\n.d-stack2 { > * + * { margin-top: @su2 !important; } }\n.d-stack4 { > * + * { margin-top: @su4 !important; } }\n.d-stack6 { > * + * { margin-top: @su6 !important; } }\n.d-stack8 { > * + * { margin-top: @su8 !important; } }\n.d-stack12 { > * + * { margin-top: @su12 !important; } }\n.d-stack16 { > * + * { margin-top: @su16 !important; } }\n.d-stack24 { > * + * { margin-top: @su24 !important; } }\n.d-stack32 { > * + * { margin-top: @su32 !important; } }\n.d-stack48 { > * + * { margin-top: @su48 !important; } }\n.d-stack64 { > * + * { margin-top: @su64 !important; } }\n.d-stack72 { > * + * { margin-top: @su72 !important; } }\n.d-stack84 { > * + * { margin-top: @su84 !important; } }\n.d-stack96 { > * + * { margin-top: @su96 !important; } }\n.d-stack102 { > * + * { margin-top: @su102 !important; } }\n.d-stack114 { > * + * { margin-top: @su114 !important; } }\n.d-stack128 { > * + * { margin-top: @su128 !important; } }\n\n.d-flow0 { > * + * { margin-left: @su0 !important; } }\n.d-flow1 { > * + * { margin-left: @su1 !important; } }\n.d-flow2 { > * + * { margin-left: @su2 !important; } }\n.d-flow4 { > * + * { margin-left: @su4 !important; } }\n.d-flow6 { > * + * { margin-left: @su6 !important; } }\n.d-flow8 { > * + * { margin-left: @su8 !important; } }\n.d-flow12 { > * + * { margin-left: @su12 !important; } }\n.d-flow16 { > * + * { margin-left: @su16 !important; } }\n.d-flow24 { > * + * { margin-left: @su24 !important; } }\n.d-flow32 { > * + * { margin-left: @su32 !important; } }\n.d-flow48 { > * + * { margin-left: @su48 !important; } }\n.d-flow64 { > * + * { margin-left: @su64 !important; } }\n.d-flow72 { > * + * { margin-left: @su72 !important; } }\n.d-flow84 { > * + * { margin-left: @su84 !important; } }\n.d-flow96 { > * + * { margin-left: @su96 !important; } }\n.d-flow102 { > * + * { margin-left: @su102 !important; } }\n.d-flow114 { > * + * { margin-left: @su114 !important; } }\n.d-flow128 { > * + * { margin-left: @su128 !important; } }\n\n\n//  ============================================================================\n//     MARGIN\n//  ============================================================================\n//  ============================================================================\n//  $  POSITIVE MARGINS\n//  ============================================================================\n//  $$ ALL DIRECTIONS\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-m',\n    { .template(@value) { margin: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ TOP MARGIN\n//  ----------------------------------------------------------------------------\n#d-internals #auto-generate(\n    '.d-mt',\n    { .template(@value) { margin-top: @value !important; } },\n    @su0 @su1 @su2 @su4 @su6 @su8 @su12 @su16 @su24 @su32 @su48 @su64 @su72 @su84 @su96 @su102 @su114 @su128 auto unset\n);\n\n//  $$ RIGHT MARGIN\n//  ---------------------------------------------------------------